import { Component, Directive, Input } from '@angular/core';
import { BbTemplate } from '@backbase/foundation-ang/core';

@Component({
  selector: 'bb-payee-item-detail',
  templateUrl: './payee-item-detail.component.html',
})
export class PayeeItemDetailComponent {
  /**
   * Component context
   */
  readonly hostRef: PayeeItemDetailComponent = this;

  @Input() payeeDetail: any;

  /**
   * Padding on top
   */
  @Input() isPaddingTop = true;

  getAddress(detail: any): string | undefined {
    const keys = ['addressLine1', 'addressLine2', 'streetName', 'postCode', 'town', 'countrySubDivision', 'country'];
    return this.getAddressString(keys, detail);
  }

  getBankAddress(account: any): string | undefined {
    const keys = [
      'bankAddressLine1',
      'bankAddressLine2',
      'bankStreetName',
      'bankPostCode',
      'bankTown',
      'bankCountrySubDivision',
      'bankCountry',
    ];
    return this.getAddressString(keys, account);
  }

  getHoldersAddress(account: any): string | undefined {
    const keys = [
      'accountHolderAddressLine1',
      'accountHolderAddressLine2',
      'accountHolderStreetName',
      'accountHolderPostCode',
      'accountHolderTown',
      'accountHolderCountrySubDivision',
      'accountHolderCountry',
    ];
    return this.getAddressString(keys, account);
  }

  getAddressString(keys: any[], detail: any): string | undefined {
    const addressData: any[] = [];
    keys.forEach((key: string) => {
      if (detail[key]) {
        addressData.push(detail[key]);
      }
    });
    return addressData.length ? addressData.join(', ') : undefined;
  }
}

@Directive({
  selector: '[bbPayeeItemDetailCustomizable]',
})
export class PayeeItemDetailCustomizableDirective extends BbTemplate<PayeeItemDetailComponent, void> {}
